<template>
    <div class="order-detail">
        <div class="detail">
            <cube-scroll>
            <div class="status">
                <div class="info">
                    <!-- <i class="icon iconfont icon-daishouhuo"></i>
                    <i class="icon iconfont icon-daifahuo"></i>
                    <i class="icon iconfont icon-daifukuan"></i> -->
                    <span class="text">{{status[orderObj.orderState]}}</span>
                </div>
            </div>
            <div class="card">
                <div class="item address">
                    <div class="info">
                        <div class="name">{{orderObj.receiveArea.receiverName}}</div>
                        <div class="phone">{{orderObj.receiveArea.phone}}</div>
                    </div>
                    <div class="info area">
                        <div class="name">地址:</div>
                        <div class="phone">{{orderObj.receiveArea.receiveArea}}</div>
                    </div>
                </div>
            </div>
            <div class="card">
                <div class="item list">
                    <div class="list-title">商品列表</div>
                    <div class="product-wrap" v-for="(x, index) in orderObj.orderDetailWxVOList" :key="index">
                        <div class="product">
                            <div class="img">
                                <img  :src="`/picture/${x.img}`" alt="">
                            </div>
                            <div class="desc">
                                <p class="name">商品类型：{{x.goodsName}}</p>
                                <p class="other">
                                    <span>数量: {{x.goodsNum}}</span>
                                    <span>材质: {{x.goodsTexture}}</span>
                                    <span>类型: {{x.goodsType}}</span>
                                    <span>方式: {{x.washMethod}}</span>
                                </p>
                                <p class="price">￥ {{x.goodsPrice}}</p>
                            </div>
                        </div>
                        <div class="btns">
                            <div class="btn m-order-btn">加购物车</div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="card">
                <div class="item order">
                    <div class="info">
                        <div class="title">订单编号：</div>
                        <div class="num">{{orderObj.orderId}}</div>
                    </div>
                    <div class="info">
                        <div class="title">下单时间：</div>
                        <div class="num">{{orderObj.startOrderTime}}</div>
                    </div>
                    <div class="info">
                        <div class="title">支付时间：</div>
                        <div class="num">{{orderObj.startOrderTime}}</div>
                    </div>
                    <div class="info">
                        <div class="title">商品数量：</div>
                        <div class="num">{{orderObj.goodsNum}}</div>
                    </div>
                    <div class="info">
                        <div class="title">金额总计：</div>
                        <div class="num">{{orderObj.orderMonery}}</div>
                    </div>
                </div>
            </div>
            </cube-scroll>
        </div>
        <div class="btns">
            <div class="left">
                <div class="btn">删除订单</div>
            </div>
            <div class="right">
                <div class="btn m-order-btn" v-if="orderObj.orderState === 1">去支付</div>
                <div class="btn m-order-btn" v-if="orderObj.orderState === 7">去评价</div>
            </div>
        </div>
    </div>
</template>
<script>
import { getOrderDetailInfomation } from "@/api/order.js";
export default {
    name: 'order-detail',
    data() {
        return {
            status: {
                1: '待付款',
                2: '待取货',
                3: '待分拣',
                4: '清洗中',
                5: '待收货',
                6: '待评价',
                7: '已完成',
                8: '已取消',
            },
            orderObj: {
                orderId: '',
                startOrderTime: '',
                goodsNum: '',
                orderMonery: '',
                receiveArea: {
                    receiverName: '',
                    phone: '',
                    receiveArea: '',
                },
                orderDetailWxVOList: [],
            },
        };
    },
    created() {
        this._getOrderDetailInfomation(this.$route.query.id)
    },
    methods: {
        _getOrderDetailInfomation(orderId) {
            let params = {
                orderId,
            }
            getOrderDetailInfomation(params).then(res => {
                this.orderObj = res;
                this.orderObj.receiveArea.receiveArea = res.receiveArea.province + res.receiveArea.city + res.receiveArea.area + res.receiveArea.address;
                console.log(this.orderObj)
            })
        },
    }
}
</script>
<style lang="stylus" scoped>
.order-detail {
    height: 100vh;
    font-family: HiraginoSansGB-W3;
    font-size: 28px;
    .detail {
        height: calc(100vh - 120px);
        .status {
            text-align: center;
            height: 250px;
            position: relative;
            // background: linear-gradient(#B03A2E, #CB4335, #EC7063);
            background: linear-gradient(#D35400, #DC7633, #E59866);
            .info {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                color: #ffffff;
                font-size: 32px;
                line-height: 32px;
                .icon, .text {
                    font-weight: bold;
                }
            }
        }
        .card {
            background-color: #ffffff;
            border-radius: 24px;
            padding: 24px;
            margin: 12px;
            .item {
                .info {
                    display: flex;
                    font-weight: bold;
                }
            }
            .address {
                .info {
                    .name {
                        margin-right: 24px;
                    }    
                }
                .area {
                    margin-top: 24px;
                    font-weight: normal;
                    color: #666666;
                    font-size: 24px;
                }
            }
            .order {
                .info {
                    padding: 12px 0;
                   .title {
                       color: #666666;
                    } 
                }
            }
            .list {
                .list-title {
                    margin-bottom: 12px;
                }
                .product-wrap {
                    border-bottom: 0.5px solid #F2F2F2;
                    padding: 12px 0;
                    .product {
                        font-size: 24px;
                        display: flex;
                        .img {
                            width: 100px;
                            height: 100px;
                            border-radius: 12px;
                            margin-right: 24px;
                            img {
                                width: 100px;
                                height: 100px;
                                border-radius: 12px;
                            }
                        }
                        .desc {
                            .name {
                                line-height: 36px;
                            }
                            .other {
                                color: #666666;
                                line-height: 36px;
                                
                                span {
                                    margin-right: 12px;
                                }
                            }
                        }
                    }
                    .btns {
                        height: 60px;
                        border: none;
                        display: flex;
                        align-items: center;
                        justify-content: flex-end;
                        .btn {
                            margin-left: 12px;
                        }
                    }
                }
            }
        }
    }
    .btns {
        border-top: 0.5px solid #F2F2F2;
        background-color: #ffffff;
        height: 120px;
        padding: 0 24px;
        display: flex;
        align-items: center;
        .left, .right  {
            flex: 1;
        }
        .left {
            display: flex;
            text-align: left ;
        }
        .right {
            display: flex;
            align-items: center;
            justify-content: flex-end;
            .btn {
                margin-left: 12px;
            }
        }
    }
}
</style>


